import React from 'react';
import { connect } from 'react-redux';
import PageHeader from './components/PageHeader';
import PageContent from './components/PageContent';
import Loader from './components/Loader';
import { actions } from '../store/Portfolio';
import Project from './portfolio/Project';
import SocialIcon from './components/SocialIcon';

class Portfolio extends React.Component {
  componentDidMount() {
    this.props.loadProjects();
  }

  render() {
    const { portfolio } = this.props;
    return (
      <div>
        <PageHeader color="is-light" title="Projects">
          Find all my open source works on
          <SocialIcon
            link="https://www.github.com/gplhegde"
            icon="fab fa-github"
            label="GitHub"
          />
        </PageHeader>
        {portfolio.isLoading ? (
          <Loader />
        ) : (
          <PageContent>
            <div className="columns is-multiline has-same-height">
              {portfolio.projects.map((project, idx) => {
                return (
                  <div className="column is-6" key={idx}>
                    <Project projectNum={idx} {...project.fields} />
                  </div>
                );
              })}
            </div>
          </PageContent>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    portfolio: state.portfolio
  };
}

function mapDispatchToProps(dispatch) {
  return {
    loadProjects: () => dispatch(actions.pending())
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Portfolio);
