import React from 'react';
import BlogNav from './BlogNav';
import BlogContent from './BlogContent';

const BlogItem = props => {
  return (
    <div className="box">
      <BlogContent limit={5} {...props}>
        <BlogNav
          date={props.date}
          linkText="Read more"
          status={props.status}
          to={{
            pathname: `/blog/${props.path}`,
            state: { props, linkText: 'Back to blog' }
          }}
        />
      </BlogContent>
    </div>
  );
};

export default BlogItem;
