import { call, put, all, takeLatest, delay } from 'redux-saga/effects';
import { actions } from '../Blog';
import * as types from './types';

import { contentfulClient } from '../index';

const fetchPosts = () => {
  return contentfulClient.getEntries({ content_type: 'blog' });
};

function* getBlogPosts() {
  try {
    const resp = yield call(fetchPosts);
    // yield delay(5000);
    yield put(actions.success(resp.items));
  } catch (err) {
    console.log(err);
    yield put(actions.error(err));
  }
}

export default function*() {
  yield all([takeLatest(types.GET_BLOG_ASYNC.PENDING, getBlogPosts)]);
}
