import React from 'react';
import classNames from 'classnames';

const SocialIcon = props => {
  const { color, link, icon, label } = props;
  return (
    <a className={classNames('button', color)} href={link} target="_blank">
      <span className="icon">
        <i className={icon} />
      </span>
      <span>{label}</span>
    </a>
  );
};

export default SocialIcon;
