import initialState from '../initialState';
import * as types from './types';
import { createReducer } from '../Utils';

export default createReducer(initialState.portfolio, {
  [types.GET_PROJECTS_ASYNC.PENDING](state) {
    return {
      ...state,
      isLoading: true
    };
  },
  [types.GET_PROJECTS_ASYNC.SUCCESS](state, action) {
    return {
      ...state,
      projects: action.projects,
      isLoading: false
    };
  },
  [types.GET_PROJECTS_ASYNC.ERROR](state) {
    return {
      ...state,
      isLoading: false,
      isError: true
    };
  }
});
