import { createAction } from './Utils';
import * as types from './portfolio/types';

export const actions = {
  pending: () => createAction(types.GET_PROJECTS_ASYNC.PENDING),
  success: projects =>
    createAction(types.GET_PROJECTS_ASYNC.SUCCESS, { projects }),
  error: error => createAction(types.GET_PROJECTS_ASYNC.ERROR, { error })
};
