import React from 'react';
import * as Markdown from 'react-markdown';
import 'react-bulma-components/dist/react-bulma-components.min.css';
import { Button, Modal, Media, Content } from 'react-bulma-components';

class Project extends React.Component {
  state = {
    isOpen: false
  };

  componentDidMount() {
    // document.addEventListener('DOMContentLoaded', () => {
    var $modalButtons = Array.prototype.slice.call(
      document.querySelectorAll(`#project-card-${this.props.projectNum}`),
      0
    );

    if ($modalButtons.length > 0) {
      $modalButtons.forEach($el => {
        $el.addEventListener('click', this.openProject);
      });
    }
    // });
  }
  openProject = () => {
    this.setState({ isOpen: true });
  };

  closeProject = () => {
    this.setState({ isOpen: false });
  };

  render() {
    const { title, description, banners, urls } = this.props;
    return (
      <div>
        <div
          className="card modal-button"
          id={`project-card-${this.props.projectNum}`}
        >
          <div className="card-header">
            <p className="card-header-title is-centered">{title}</p>
          </div>
          <div className="card-image">
            <figure className="image is-4by3">
              <img src={banners[0]} alt="Banner" />
            </figure>
          </div>
        </div>
        <Modal
          show={this.state.isOpen}
          onClose={this.closeProject}
          closeOnEsc={true}
        >
          <Modal.Card>
            <Modal.Card.Head>
              <Modal.Card.Title className="is-centered">
                {title}
              </Modal.Card.Title>
            </Modal.Card.Head>
            <Modal.Card.Body>
              <Media>
                <Media.Item>
                  <Content>
                    <Markdown source={description} />
                  </Content>
                </Media.Item>
              </Media>
            </Modal.Card.Body>
            <Modal.Card.Foot>
              <a
                className="card-footer-item button is-link is-medium is-outlined"
                target="_blank"
                href={urls.demoUrl}
              >
                Demo
              </a>
              <a
                className="card-footer-item button is-link is-medium is-outlined"
                target="_blank"
                href={urls.sourceCodeUrl}
              >
                Code
              </a>
            </Modal.Card.Foot>
          </Modal.Card>
        </Modal>
      </div>
    );
  }
}

export default Project;
