import { call, put, all, takeLatest } from 'redux-saga/effects';
import { actions } from '../Portfolio';
import * as types from './types';
import { contentfulClient } from '../index';

const fetchProjects = () => {
  return contentfulClient.getEntries({ content_type: 'project' });
};

function* getProjects() {
  try {
    const resp = yield call(fetchProjects);
    yield put(actions.success(resp.items));
  } catch (err) {
    console.log(err);
    yield put(actions.error(err));
  }
}

export default function*() {
  yield all([takeLatest(types.GET_PROJECTS_ASYNC.PENDING, getProjects)]);
}
