import React from 'react';
import { connect } from 'react-redux';
import BlogItem from './blog/BlogItem';
import PageHeader from './components/PageHeader';
import PageContent from './components/PageContent';
import Loader from './components/Loader';
import { actions } from '../store/Blog';

class Blog extends React.Component {
  componentDidMount() {
    this.props.loadBlogs();
  }
  render() {
    const { blog } = this.props;
    return (
      <div>
        <PageHeader color="is-light" title="Blog arena">
          Welcome to my blog. Hope you enjoy it!
        </PageHeader>
        {blog.isLoading ? (
          <Loader />
        ) : (
          <PageContent>
            {blog.posts.map((post, idx) => {
              return <BlogItem key={idx} {...post.fields} />;
            })}
          </PageContent>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    blog: state.blog
  };
}

function mapDispatchToProps(dispatch) {
  return {
    loadBlogs: () => dispatch(actions.pending())
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Blog);
